import React, { useContext } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import { ThemeContext } from 'providers/ThemeProvider';
import { Container, Button } from 'components/common';
import dev from 'assets/illustrations/skills.svg';
import { Wrapper, SkillsWrapper, Details, Thumbnail } from './styles';
import './Skill.css'
import '@fortawesome/fontawesome-free/css/all.min.css'; 
import 'bootstrap-css-only/css/bootstrap.min.css'; 
import 'mdbreact/dist/css/mdb.css';
import { MDBIcon } from 'mdbreact';



export const Skills = () => {
  const { theme } = useContext(ThemeContext);

  const skillsSection = {

    softwareSkills: [
      {
        skillName: "html-5",
        // fontAwesomeClassname: "fab fa-html5",
        icon: "html5"
      },
      {
        skillName: "css3",
        // fontAwesomeClassname: "fab fa-css3-alt",
        icon: "css3-alt"
      },
      {
        skillName: "JavaScript",
        // fontAwesomeClassname: "fab fa-js",
        icon: "js"
      },
      {
        skillName: "reactjs",
        // fontAwesomeClassname: "fab fa-react",
        icon: "react"
      },
      {
        skillName: "nodejs",
        // fontAwesomeClassname: "fab fa-node",
        icon: "node"
      },
      {
        skillName: "npm",
        // fontAwesomeClassname: "fab fa-npm",
        icon: "npm"
      },
      {
        skillName: "mongodb",
        // fontAwesomeClassname: "fas fa-database",
        icon: "mdb"
      },
      {
        skillName: "aws",
        // fontAwesomeClassname: "fab fa-aws",
        icon: "aws"
      },
      {
        skillName: "python",
        // fontAwesomeClassname: "fab fa-python",
        icon: "python"
      }
    ],
    display: true // Set false to hide this section, defaults to true
  };

  return (
    <Wrapper id="about">
      <SkillsWrapper as={Container}>
        <Thumbnail>
          <img src={dev} alt="Im Mahesh and Im a Full Stack Developer!" />
        </Thumbnail>
        <Details theme={theme}>
          <h1>More about me</h1>
          <p>
          Creative Full-Stack developer passionate about constantly learning new things has led to a
          recent bootcamp certification from Guvi Geek(Zen Class). Demonstrating a desire to stay current on the
          latest technologies being used in IT.
          </p>
          <div>
      <div className="software-skills-main-div">
        <ul className="dev-icons">
          {skillsSection.softwareSkills.map((skills, i) => {
            return (
              <li
                key={i}
                className="software-skill-inline"
                name={skills.skillName}
              >
                {/* <i className={skills.fontAwesomeClassname}></i> */}
                 {/* <FontAwesomeIcon icon={faReact} /> */}
                <MDBIcon fab icon={skills.icon} />
                <p>{skills.skillName}</p>
              </li>
            );
          })}
        </ul>
      </div>
      </div>
        </Details>
      </SkillsWrapper>
    </Wrapper>
  );
};
